<template>
  <div class="utils-datetime-view">{{ text }}</div>
</template>
        
<script>
import TimeJS from "@/js/time.js";

export default {
  name: "utils-datetime-view",

  //   components: { MyTable, MyToolbar },

  computed: {
    dateObject() {
      let o = this.date;
      let n = this.modelValue;
      if (o == null) {
        if (n == null) {
          n = 0;
        }
        o = new Date(n);
      }
      return o;
    },

    text() {
      let d = this.dateObject;
      let fmt = this.format;
      return TimeJS.StringifyDate(d, fmt);
    },
  },

  data() {
    return {};
  },

  methods: {},

  props: {
    modelValue: Number, // a unix-style int64 timestamp
    format: String, // ["date"|"time"|"datetime"]
    date: Object, // a Date object
  },
};
</script>
      
<style scoped>
.utils-datetime-view {
  display: inline-block;
}
</style>
