<template>
  <el-dialog title="导入数据" @open="init">
    <el-form>
      <el-form-item label="备份文件路径">
        <el-input v-model="item.backup_file_path"></el-input>
      </el-form-item>
    </el-form>
    <div>
      <el-button type="success" @click="apply"> 应用 </el-button>
    </div>
  </el-dialog>
</template>
        
<!-- <script setup>
import { Refresh, Download, Upload } from "@element-plus/icons-vue";
</script> -->
  
<script>
export default {
  name: "settings-backups-import-dialog",

  // components: { MyTable, MyToolbar },

  data() {
    return {
      item: {
        backup_file_path: "",
      },
    };
  },

  methods: {
    init() {
      let path = this.path;
      if (path == null) {
        path = "";
      }
      this.item.backup_file_path = path;
    },

    apply() {
      let item = this.item;
      let method = "POST";
      let type = "backups/import";
      let data = {
        backups: [item],
      };
      this.$store.dispatch("rest/execute", { method, type, data }).then(() => {
        this.close();
      });
    },

    close() {
      this.$emit("update:modelValue", false);
    },
  },

  props: {
    path: String,
  },
};
</script>
  
