<template>
  <div class="settings-backups-table">
    <el-table :data="items">
      <el-table-column prop="backup_file_name" label="文件名" sortable>
        <template #default="scope">
          <div :title="scope.row.backup_file_path">
            {{ scope.row.backup_file_name }}
          </div>
        </template>
      </el-table-column>

      <el-table-column prop="created_at" label="创建时间" sortable>
        <template #default="scope">
          <DateTimeView v-model="scope.row.created_at" />
        </template>
      </el-table-column>

      <el-table-column label="操作" width="170px">
        <template #default="scope">
          <el-button
            link
            type="primary"
            @click="handleClickItemImport(scope.row)"
            >导入</el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <MyImportDialog v-model="displayImportDialog" :path="theImportPath" />
  </div>
</template>
    
<script>
import DateTimeView from "@/components/utils/date_time_view.vue";
import MyImportDialog from "./import-dialog.vue";

export default {
  name: "settings-backups-table",

  components: { DateTimeView, MyImportDialog },

  computed: {
    items() {
      return this.$store.getters["backup/list"];
    },
  },

  data() {
    return {
      theImportPath: "",
      displayImportDialog: false,
    };
  },

  methods: {
    handleClickItemImport(item) {
      this.theImportPath = item.backup_file_path;
      this.displayImportDialog = true;
    },
  },
};
</script>
  
