<template>
  <el-dialog title="导出数据" @open="init">
    <div>
      <el-form>
        <el-form-item label="文件名">
          <el-input v-model="filename"></el-input>
        </el-form-item>
      </el-form>

      <el-button type="success" @click="save"> 保存 </el-button>
    </div>
  </el-dialog>
</template>
          
  <!-- <script setup>
  import { Refresh, Download, Upload } from "@element-plus/icons-vue";
  </script> -->
    
<script>
import TimeJS from "@/js/time.js";

export default {
  name: "settings-backups-export-dialog",

  // components: { MyTable, MyToolbar },

  data() {
    return {
      filename: "",
    };
  },

  methods: {
    save() {
      let name = this.filename;
      let item = {
        backup_file_name: name,
      };
      let method = "POST";
      let type = "backups/export";
      let data = {
        backups: [item],
      };
      this.$store.dispatch("rest/execute", { method, type, data }).then(() => {
        this.close();
        this.refresh();
      });
    },

    makeNewFileName() {
      let now = new Date();
      let str1 = TimeJS.StringifyDate(now, "date");
      let str2 = TimeJS.StringifyDate(now, "time");
      str1 = this.makePureNumberString(str1);
      str2 = this.makePureNumberString(str2);
      return "wpm-backup-" + str1 + "-" + str2 + ".json";
    },

    makePureNumberString(str1) {
      let str2 = "";
      let len = str1.length;
      for (let i = 0; i < len; i++) {
        let ch = str1.charAt(i);
        if ("0" <= ch && ch <= "9") {
          str2 += ch;
        }
      }
      return str2;
    },

    refresh() {
      this.$store.dispatch("backup/fetch", {});
    },

    close() {
      this.$emit("update:modelValue", false);
    },

    init() {
      this.filename = this.makeNewFileName();
    },
  },

  props: {},

  watch: {},
};
</script>
    
