<template>
  <div class="settings-backups-toolbar">
    <el-button title="刷新" @click="refresh">
      <el-icon> <Refresh /> </el-icon>
    </el-button>

    <el-button title="导出" @click="exportToFile">
      <el-icon> <Upload /> </el-icon> 导出到文件
    </el-button>

    <el-button title="导入" @click="importFromFile">
      <el-icon> <Download /> </el-icon> 从文件导入
    </el-button>
  </div>
</template>
      
<script setup>
import { Refresh, Download, Upload } from "@element-plus/icons-vue";
</script>

<script>
export default {
  name: "settings-backups-toolbar",

  // components: { MyTable, MyToolbar },

  data() {
    return {};
  },

  methods: {
    fireAction(name) {
      let e = { name };
      this.$emit("action", e);
    },

    refresh() {
      this.fireAction("refresh");
    },

    importFromFile() {
      this.fireAction("import");
    },

    exportToFile() {
      this.fireAction("export");
    },
  },
};
</script>

