<template>
  <div class="settings-backups-index-page">
    <h1>数据导入/导出</h1>

    <MyToolbar class="settings-backups-toolbar" @action="handleAction" />

    <MyTable @action="handleAction" />

    <MyExportDialog v-model="displayExportDialog" />

    <MyImportDialog v-model="displayImportDialog" :path="theImportPath" />
  </div>
</template>
    
<script>
import MyTable from "./table.vue";
import MyToolbar from "./toolbar.vue";
import MyExportDialog from "./export-dialog.vue";
import MyImportDialog from "./import-dialog.vue";

export default {
  name: "settings-backups-index",

  components: {
    MyTable,
    MyToolbar,
    MyExportDialog,
    MyImportDialog,
  },

  data() {
    return {
      displayExportDialog: false,
      displayImportDialog: false,
      theImportPath: "",
    };
  },

  methods: {
    handleAction(e) {
      let name = e.name;
      if (name == "edit") {
        this.edit(e.item);
      } else if (name == "export") {
        this.showExportDialog();
      } else if (name == "import") {
        this.showImportDialog();
      } else if (name == "refresh") {
        this.fetch();
      } else if (name == "add") {
        this.add();
      }
    },

    fetch() {
      this.$store.dispatch("backup/fetch");
    },

    add() {
      this.theAddItem = {};
      this.theAddEnabled = true;
    },

    edit(item) {
      this.theEditItem = item;
      this.theEditEnabled = true;
    },

    showImportDialog() {
      this.displayImportDialog = true;
    },

    showExportDialog() {
      this.displayExportDialog = true;
    },
  },

  mounted() {
    this.fetch();
  },
};
</script>
  
<style scoped>
.settings-backups-index-page {
  text-align: left;
}

.settings-backups-toolbar {
  margin-top: 50px;
  margin-bottom: 20px;
  text-align: right;
}
</style>
  